@import url(https://en.wikipedia.org/w/load.php?debug=false&lang=en&modules=mediawiki.legacy.commonPrint,shared|skins.vector.styles&only=styles&skin=vector&*);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes rotate {
  to {
    transform: rotate(360deg) translate(-50%, -50%);
  }
}

@keyframes rotate {
  to {
    transform: rotate(360deg) translate(-50%, -50%);
  }
}


.App-header,
.App-btns {
  text-align: center;
  margin-bottom: 25px;
}

.wiki-btn {
  margin: 0 15px;
  border: none;
  background-color: lightgray;
  padding: 7px 15px;
  border-radius: 4px;
  cursor: pointer;
  outline: 0;
  text-transform: capitalize;
}

.wiki-btn.selected {
  background-color: #39869e;
  color: white
}

.wiki-btn:disabled {
  cursor: default;
}


.wiki-wrapper {
  position: relative;
  padding: 25px;
  width: 90%;
  display: block;
  min-height: 200px;
  margin: 0 auto;
  border: 1px solid #dedede;
}


.loading-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(0) translate(-50%, -50%);
  transform-origin: top left;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 5px solid grey;
  border-top-color: black;
  -webkit-animation: rotate 1s infinite;
          animation: rotate 1s infinite
}
